import "./App.css";
import "./styles/StationsTables.css";
import { useState } from "react";
import React, { useEffect } from "react";
import { MapContainer, TileLayer, Popup, CircleMarker, useMap} from "react-leaflet";
import Axios from "axios";

import LocationPopup from "./components/LocationPopup";
import SideCirles from "./components/SideCircles";
import StationsTable from "./components/StationsTable";

export const circleClasses=['circleA95', 'circleA98', 'circleD', 'circleLPG']
export const circleText=['95', '98', 'D', 'LPG']

export const api = "https://fuelapi.ignuxas.com"

function App() {
  var Map;
  var menuOpen = false;

  const [FuelStationData, SetFuelStationData] = useState([]);
  const [DisplayedTableStations, setDisplayedTableStations] = useState(12);
  const [ErrorCode, setErrorCode] = useState(200);

  const getlocations = () => {
    Axios.get(api+"/fuelstations").then((response) => {
      if(response.status === 200){
        SetFuelStationData(response.data);
        console.log("Got Data");
        document.getElementById("Loading").style.display = "none";
      } else {
        setErrorCode(response.status);
        document.getElementById("Loading").style.display = "none";
      }
    }).catch((error) => {
      setErrorCode(500);
      document.getElementById("Loading").style.display = "none";
    });
  };

  useEffect(() => {
    getlocations();
  }, []);

  function getMinStation(array, average) {
    var minNum = 99;
    var stationNum = 0;

    for (var i = 0; i < array.length; i++) {
      if (array[i] !== null && array[i] > average - 0.3) { // filter out troll and null data
        if (array[i] < minNum) {
          minNum = array[i];
          stationNum = i;
        }
      }
    }
    console.log("Min Price: " + minNum);

    return FuelStationData[stationNum];
  }

  function averagePrice(array){
    var trueLength = 0;
    var value = 0

    for (var i = 0; i < array.length; i++) {
      if (array[i] !== null && array[i] > 0.4 && array[i] < 3) { // filter out troll and null data
        trueLength += 1
        value += array[i]
      }
    }
    return (value / trueLength)
  }

  const MapController = (position) => {
    const map = useMap();
    Map = map;
    return <></>;
  };

  function setPos(position, zoom=Map.getZoom(), offset=[30000,0]) {
    Map.flyTo(position, zoom);
  }

  if (FuelStationData.length !== 0) {
    //there is a better way to do this, will re-write sometime

    const A95Prices = [];
    const A98Prices = [];
    const DPrices = [];
    const LPGPrices = [];

    for (let i = 0; i < FuelStationData.length; i++) {
      A95Prices.push(FuelStationData[i].A95);
      A98Prices.push(FuelStationData[i].A98);
      DPrices.push(FuelStationData[i].D);
      LPGPrices.push(FuelStationData[i].LPG);
    }
    //average prices
    var avgA95Price = averagePrice(A95Prices);
    var avgA98Price = averagePrice(A98Prices);
    var avgDPrice = averagePrice(DPrices);
    var avgLPGPrice = averagePrice(LPGPrices);

    var averagePrices = [avgA95Price, avgA98Price, avgDPrice, avgLPGPrice];

    //min prices
    var minA95PriceStation = getMinStation(A95Prices, avgA95Price);
    var minA98PriceStation = getMinStation(A98Prices, avgA98Price);
    var minDPriceStation = getMinStation(DPrices, avgDPrice);
    var minLPGPriceStation = getMinStation(LPGPrices, avgLPGPrice);

    var minPriceStations = [minA95PriceStation, minA98PriceStation, minDPriceStation, minLPGPriceStation];
  }

  function openMenu(){
    var menu = document.querySelector("#Menu");
    if(menuOpen === false){ // OPEN menu
      menu.style.left = "0px";
      menuOpen = true;
    }
    else{
      menu.style.left = "-320px";
      menuOpen = false;
    }
  }


  return (
    <div className="App">
      <div className="lds-dual-ring" id="Loading" />
      <div id="ErrorDiv" style={ErrorCode !== 200 ? {display:"block"}:{display:"none"}}>
        <h2>Error {ErrorCode}</h2>
        The server is currently down, please try again later or contact me at <a className="aeffect" href="mailto:mikolaitis.ignas@gmail.com">mikolaitis.ignas@gmail.com</a>
      </div>
      {FuelStationData[0] ? (
      <div id="Menu">
        <div id="StationPanel">
            <p id="Title">KURAS</p>
            <div id="StationTable">
              <StationsTable 
                FuelStationData={FuelStationData}
                averagePrices={averagePrices}
                quantity={DisplayedTableStations}
                setDisplayedTableStations={setDisplayedTableStations}
                setPos={setPos}
              ></StationsTable>
            </div>
            <footer>
                <p>Fuel Map made by: <a className="aeffect" href="https://ignuxas.com" target="_blank" rel="noreferrer">Ignas Mikolaitis</a></p>
                <p className="betaText">OPEN BETA: This project is still in development and anything can change, there may be some bugs and unfinished features.</p>
                <p className="redText">Note: The prices are crowd sourced and may be inaccurate or outdated.</p>
            </footer>
        </div>
        <div id="PricePanel">
            <i className="fas fa-bars" onClick={() => openMenu()} />
            <div className="avgFuelPrices">
                <SideCirles 
                  averagePrices={averagePrices} 
                  setPos={setPos} 
                  minPriceStations={minPriceStations}
                  minPrices={[minA95PriceStation.A95, minA98PriceStation.A98, minDPriceStation.D, minLPGPriceStation.LPG]}
                  ></SideCirles>
            </div>
        </div>
    </div>
    ) : (<></>)
    }

      <div id="map">
        <MapContainer
          whenCreated={(map) => {
              Map = map
          }}
          center={[55.14952486735533, 23.992860624307255]}
          zoom={8}
          zoomControl={false}
          preferCanvas={true}
        >
          <MapController></MapController>
          <TileLayer
            attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors &copy; <a href="https://carto.com/attributions">CARTO</a>'
            url="https://{s}.basemaps.cartocdn.com/dark_all/{z}/{x}/{y}{r}.png"
            subdomains="abcd"
            maxZoom="20"
          />
          {FuelStationData.map((location) => (
            // will update this when I update the database
            location.A95 || location.A98 || location.D || location.LPG ? (
              <CircleMarker
                center={[location.latitude, location.longitude]}
                radius={9}
                pathOptions={{
                  color: "#00AC2B",
                  fillOpacity: 1,
                  fillColor: "#57FF71",
                  bubblingMouseEvents: false,
                  weight: 2.5,
                }}
                class="Marker"
              >
                <Popup>
                  <LocationPopup 
                  location={location} 
                  averagePrices={averagePrices}></LocationPopup>
                </Popup>
              </CircleMarker>
            ) : (<></>)
          ))}
        </MapContainer>
      </div>
    </div>
  );
}

export default App;
