import { circleClasses, circleText } from "../App";

export default function FuelTypeCircles(props) {
    const averagePrices = props.averagePrices;
    const minPriceStations = props.minPriceStations;
    const minPrices = props.minPrices;
    
    return (
        averagePrices.map((averagePrice, index) => (
            <div className={`circleDisplay ${circleClasses[index]}`}>
                <svg height="60" width="60"><circle cx="30" cy="30" r="30"/>
                </svg>
                <h4 className="iconTextDisplay">{circleText[index]}</h4>
                <div className="fuelPricesDiv">
                    <h4 >VID: {averagePrice.toFixed(2)}€</h4>
                    <h4 className="minPrice" 
                        onClick={() => props.setPos([minPriceStations[index].latitude, minPriceStations[index].longitude])}> 
                        MIN: {minPrices[index].toFixed(2)}€</h4>
                </div>
            </div>
        ))
    )
}