import { circleClasses, circleText } from "../App";

export default function FuelTypeCircles(props) {
    const fuelPrices = props.fuelTypes;
    const averagePrices = props.avgPrices;

    return (
        fuelPrices.map((fuelPrice, index) => {
            return (
                fuelPrice > 0 && fuelPrice !== null ? (
                <div className={`circle ${circleClasses[index]}`}>
                    <svg height="30" width="30">
                        <circle cx="15" cy="15" r="15" />
                    </svg>
                    <h4 className="iconText">{circleText[index]}</h4>
                    <div contenteditable={props.editableField === props.stationID ? "true":"false"} 
                         className={`price ${averagePrices[index] > fuelPrice ? ("priceGreen") : ("priceRed")}`}>
                            <span contentEditable="false">€</span>&nbsp;{fuelPrice /* todo: fix warnings */}
                        </div>
                </div>
                ) : (<></>)
            )
        })
    )
}